<script setup lang="ts" generic="TModel extends ListboxValue = DefaultValue, TBy extends keyof TModel = 'value'">
import { type InputHTMLAttributes, computed } from 'vue'
import { useMediaQuery, useVModel } from '@vueuse/core'
import { Listbox } from '@headlessui/vue'
import { CheckIcon } from '@heroicons/vue/20/solid'

import type { ExtractComponentProps } from '@/shared/lib/utils'
import { type AsTag, type ClassValue, cn } from '@/shared/lib/style'

import PListboxButton from './PListboxButton.vue'
import PListboxOptions from './PListboxOptions.vue'
import PListboxOption from './PListboxOption.vue'

export type ListboxValue = Record<string, any>
export type DefaultValue = { label: string; value: number }

export type ListBoxProps = ExtractComponentProps<typeof Listbox>

export interface Props<
  TModel extends ListboxValue = DefaultValue, TBy extends keyof TModel = 'value',
 > extends /* @vue-ignore */ Omit<ListBoxProps, 'modelValue' | 'by'> {
  class?: ClassValue
  rootClass?: ClassValue
  list?: TModel[]
  /** @default 'value' */
  by?: TBy
  modelValue?: TModel[TBy] | null
  /** @default 'label' */
  label?: keyof TModel
  as?: AsTag
  nativeAttrs?: InputHTMLAttributes
}

export type Emits<TModel extends ListboxValue> = {
  'update:modelValue': [value: TModel]
}

export interface Slots<TModel extends ListboxValue> {
  default?(props: { value: TModel; open: boolean; disabled: boolean }): any
}

const props = withDefaults(defineProps<Props<TModel, TBy>>(), { as: 'div' })

const by = computed(() => props.by ?? 'value')
const label = computed(() => props.label ?? 'label')

const emits = defineEmits<Emits<TModel>>()

defineSlots<Slots<TModel>>()

defineOptions({ inheritAttrs: false })

const selectedLabel = computed(() => {
  const selected = props.list?.find((item) => item[by.value] === props.modelValue)

  return selected?.[label.value]
})

const vModel = useVModel(props, 'modelValue', emits)

const isTouchDevice = useMediaQuery('(pointer: coarse)')
</script>

<template>
  <Listbox
    v-if="!isTouchDevice"
    v-slot="slotProps"
    v-bind="$attrs"
    v-model="vModel"
    :class="props.rootClass"
    as="div"
  >
    <slot v-bind="slotProps">
      <div class="relative">
        <PListboxButton :class="props.class" :value="selectedLabel" />

        <Transition
          leave-active-class="transition ease-in duration-100"
          leave-from-class="opacity-100"
          leave-to-class="opacity-0"
        >
          <PListboxOptions>
            <PListboxOption
              v-for="item in list"
              :key="item[by]"
              v-slot="{ active, selected }"
              :value="item[by]"
            >
              <li
                class="relative cursor-default select-none py-2 pl-3 pr-9"
                :class="[active ? 'bg-indigo-600 text-white' : 'text-gray-900']"
              >
                <span
                  class="block truncate"
                  :class="[selected ? 'font-semibold' : 'font-normal']"
                >
                  {{ item[label] }}
                </span>

                <span
                  v-if="selected"
                  class="absolute inset-y-0 right-0 flex items-center pr-4"
                  :class="[active ? 'text-white' : 'text-indigo-600']"
                >
                  <CheckIcon class="h-5 w-5" aria-hidden="true" />
                </span>
              </li>
            </PListboxOption>
          </PListboxOptions>
        </Transition>
      </div>
    </slot>
  </Listbox>
  <select
    v-else
    v-model="vModel"
    v-bind="nativeAttrs"
    :class="
      cn(
        `relative min-h-10 cursor-default rounded-md border-gray-200 bg-white
        py-1.5 pl-3 pr-10 text-left text-gray-900 shadow-sm ring-1
        ring-inset ring-gray-300 focus:outline-none focus:ring-2
        focus:ring-indigo-600 sm:text-sm sm:leading-6`,
        props.class,
      )
    "
  >
    <option
      v-for="item in list"
      :key="item[by]"
      :value="item[by]"
    >
      {{ item[label] }}
    </option>
  </select>
</template>
