    v-slot="slotProps"
    :class="cn(
      `relative min-h-10 w-full cursor-default rounded-md bg-white py-1.5 pl-3
       pr-10 text-left text-gray-900 shadow-sm ring-1 ring-inset
       ring-gray-300 focus:outline-none focus:ring-2
       focus:ring-indigo-600 sm:text-sm sm:leading-6`,
      props.class,
    )"
    :as="as"
    :disabled="disabled"
  >
    <slot v-bind="slotProps">
      <span class="block truncate">{{ value ?? slotProps.value }}</span>
      <span
        class="
        pointer-events-none absolute
        inset-y-0 right-0 flex items-center pr-2
        "
      >
        <ChevronUpDownIcon
          class="h-5 w-5 text-gray-400"
          aria-hidden="true"
        />
      </span>
    </slot>
  </ListboxButton>
</template>
