<script setup lang="ts">
import { ListboxOption } from '@headlessui/vue'
import type { Component } from 'vue'
import type { ClassValue } from 'clsx'

interface Props {
  class?: ClassValue
  is?: string | Component
  value: any
}

const props = withDefaults(defineProps<Props>(), {
  is: 'template',
})
</script>

<template>
  <ListboxOption
    v-slot="slotProps"
    :as="is"
    :value="value"
    :class="props.class ?? ''"
  >
    <slot v-bind="slotProps" />
  </ListboxOption>
</template>
